 
.video-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
 
.bg-video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%);
  z-index: -1;
}
 
.content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #fff;
  text-align: center;
}
 
.HB {
  font-family: 'Arial', sans-serif;
  font-size: 2.5rem;
  margin-bottom: 20px;
}
 
.message {
  font-family: 'Arial', sans-serif;
  font-size: 1.2rem;
  margin-bottom: 20px;
}
 