/* Container for the form and video */
.form-container {
    position: relative;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px; 
}

#username{
	padding:16px;
	border: 2px solid #ddd;
}
/* Styling for the background video */
.background-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;  
    z-index: -1;  
}
 
.form-content {
    position: relative;  
    z-index: 1;  
}

.login-form {
    text-align: center;
    width: 90%;  
    max-width: 400px;  
    background-color:  rgb(2 5 6 / 70%);
    border-radius: 8px;
    padding: 20px; 
    box-shadow: 0 4px 8px 2px rgba(0, 0, 0, 0.2);  
}

 
.input-wrapper {
    position: relative;
    margin-bottom: 20px; 
}
 
.floating-input {
    width: 100%;
    padding: 16px;
    border: 2px solid #ddd; 
    border-radius: 4px;
    font-size: 16px;
    background-color: rgba(255, 255, 255, 0.2);  
    box-sizing: border-box;
    outline: none;  
    color: #fff; 
}

/* Label styling */
.input-wrapper label {
	font-family:robotm;
    position: absolute;
    top: 50%;
    left: 16px;
    font-size: 16px;
    color: #aaa;
    pointer-events: none;   
    transition: all 0.2s ease-out;
    transform: translateY(-50%);  
}

.floating-input:focus + label,
.floating-input:not(:placeholder-shown) + label {
    top: 0;
    left: 12px;
    font-size: 12px;
    color: #00aaff;  
    transform: translateY(-100%);  
	
}

/* Submit button styling */
.submit-btn {
    padding: 12px 20px;  
    border-radius: 4px;
    border: none;
    color: #fff; 
    background-color: #00aaff;  
    cursor: pointer;  
    font-size: 16px;  
    transition: background-color 0.3s, transform 0.2s;  
}

.submit-btn:hover {
    background-color: #0088cc;  
    transform: scale(1.05);  
}

.account-prompt {
    margin-top: 15px;
    text-align: center;
}

.account-prompt a {
    color: #007bff;
    /* color: #WHITER; */
    text-decoration: none;
}
.account-prompt p {
    color: #bdc5c3;
}

.account-prompt a:hover {
    text-decoration: underline;
}

