.IMGS-CC {
  display: flex;
  justify-content: left;
  align-items: center; /* Align items vertically centered */
  border: 1px solid #c8bcbc5e;
  border-radius: 12px;
  margin: 12px 10px;
  padding: 10px; /* Added padding for inner spacing */
}

.IMGS-CC img {
  width: 25%;
  height: auto;
  border-radius: 22px;
  padding: 7px;
  box-shadow: 5px 10px 26px #00000030;
  transition: transform 0.2s ease; /* Smooth transition on hover */
}

.IMGS-CC img:hover {
  box-shadow: 5px 10px 26px #00000040;
  transform: scale(1.05);
}

.desc-CC {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  margin-bottom: 20px; /* Adjust margin for spacing */
}

.IMGS-CC .desc-CC h4 {
  font-size: 18px; /* Adjust font size for mobile */
  margin-top: 5px; /* Adjust margin for spacing */
  margin-bottom: 5px;
}

.IMGS-CC .desc-CC h5 {
  /* /* font-size: 18px;   */
  margin-top: 15px;
  margin-bottom: 5px;
  color: #088178;
}
