  /* font-size: 18px;   */
  /* margin-top: 15px; */
  /* margin-bottom: 5px; */
  /* color: #088178; */
/* } */

.btn-CC {
  display: flex; /* Ensure buttons and input are flex items */
  align-items: center;
  position: relative;
  /* margin-top: 10px;  Adjust margin for spacing */ 
    margin-right: -200px;
	margin-bottom: 30px;
  right: 0; /* Reset position for smaller screens */
}

.btn-CC button,
.btn-CC input {
  margin-left: 10px; /* Adjust margin for spacing */
  width: 30px; /* Adjust width for smaller screens */
  height: 30px;
  padding: 2px;
  border: none;
  background-color: #f0f0f0;
  cursor: pointer;
}

.btn-CC input {
  width: 50px; /* Adjust width for input field */
  text-align: center;
}

.btn-CC button:hover,
.btn-CC input:hover {
  background-color: #e0e0e0;
}

@media (max-width: 600px) {
  .IMGS-CC {
    flex-direction: column; /* Stack items vertically */
    align-items: flex-start; /* Align items at the start */
  }

  .IMGS-CC img {
    max-width: 100%; /* Adjust image width to fit container */
  }

  .desc-CC {
    margin-left: 0; /* Remove left margin for description */
    margin-top: 10px; /* Add top margin for spacing */
  }

  .btn-CC {
    flex-direction: row; /* Stack buttons and input horizontally */
    margin-top: 10px;
  }
}

.css-button-3d--red{
