.empty-cart-message-CCc {
    position: relative;
    height: 100vh; /* Adjust height as needed */
    overflow: hidden;
}

.background-video-CCc {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the video covers the entire container */
    z-index: -1; /* Pushes the video behind the text content */
}

.content-CCc {
    position: relative;
    z-index: 1;
    text-align: center;
    padding-top: 50px;
    color: #fcc;
    -webkit-text-stroke: 0.2px #44223a;
    font-weight: bold;
    font-family: Arial, sans-serif;
}



.content-CCc h1 {
    font-size: 3rem; /* Example font size for heading */
    margin-bottom: 20px; /* Bottom margin for separation */
}

.content-CCc p {
    font-size: 1.5rem; /* Example font size for paragraph */
}
