
 /* About.css */

.aboutclass {
  /* background:linear-gradient(123deg, #171717f2  40%,#252d3d, #4f4830f5, #263232,#403131,#10444a); */
  background:linear-gradient(123deg, #171717f2  35%,#1f293b,#2f2410f3 ,#4f4830db, #263232,#403131,#10444a);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-size: cover;
  background-position: center;
  color: white;
   
}

 

.about-content {
  flex: 1;
  padding-right: 20px; /* Spacing between text content and image */
  margin-right: 50%; /* Spacing between text content and image */
}

.about-content h1 {
	font-family:robotm;
   font-size: 2.5rem;
  margin-bottom: 50px;
  margin-top: 100px;
}

.about-content p {
	font-family:robotl;
   font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 20px;
}

.about-image {
 
  flex: 1;
  text-align: center; /* Center image horizontally */
}

.about-image img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

   