.btn {
	display:grid;
	grid-template-columns :repeat(4,1fr);
}
.btn button{
	 padding:20xp;
	 font-size:1.5rem;
	 border:1px solid #444;
	 background-color:#333;
	 color:white;
	 cursor:pointer;
}

.btn button.zero {
	gird-column:span 2;
}

.btn button:hover {
	background-color:#555;
}

