
    /* width */
    ::-webkit-scrollbar {
      width: 12px;
    }

    /* Track */
    ::-webkit-scrollbar-track {
      box-shadow: inset 0 0 5px #aea5b0;
    }

    /* Handle */
    ::-webkit-scrollbar-thumb {
      background: #b1c4bc;
    }

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
      background: #718079;
    }
  
  
   
