/* Card.css */

.card {
  text-align: center;
  display: flex;
  flex-direction: column; /* Adjusted to column layout for better alignment */
  align-items: center; /* Center align items horizontally */
  padding: 20px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 15px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
}

.card:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.Pro {
  width: 100%; /* Ensures item takes full width */
  max-width: 300px; /* Limits maximum width for better responsiveness */
  padding: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.Pro:hover {
  transform: translateY(-5px); /* Lifts card slightly on hover */
}

.Pro .PDF {
  width: 100%; /* Ensures image takes full width of container */
  border-radius: 15px;
  margin-bottom: 10px;
}

.start {
  display: flex;
  justify-content: left; /* Centers stars horizontally */
  margin-bottom: 10px;
  margin-top:5px;
  
}

.start img {
  width: 20px; /* Adjusts the size of the star images */
  height: auto; /* Ensures aspect ratio is maintained */
  margin-right: 5px; /* Adds space between each star */
}

.Pro .desc {
  text-align: left; /* Aligns text content to the left */
}

.Pro .desc span {
	font-family:robotl;
  color: #606063;
  font-size: 12px;
}

.Pro .desc h5 {
	font-family:robotm;
  color: #1a1a1a;
  font-size: 14px;
  margin-top: 5px; /* Adds spacing between elements */
}

.Pro .desc h4 {
  color: #088178;
  font-size: 15px;
  font-weight: 700;
  margin-top: 5px;
}

.Pro button {
  font-weight: 500;
  background-color: #e8f6ea;
  border: 1px solid #cce7d0;
  border-radius: 12px;
  padding: 10px;
  cursor: pointer;
  margin-top: 10px; /* Adds space between content and button */
  box-shadow: 0 4px 8px rgba(146, 169, 193, 0.3);
}

.Pro button:hover{
	
	background: linear-gradient(200deg, #e8f6ea 35%, #d4e3d7 60%);
	  box-shadow: 0 8px 16px rgba(100, 120, 140, 0.3); 
	}
	
	
.Pro button:active {
  transform: scale(0.95);  
  background-color: #cce7d0; 
   box-shadow: 0 2px 4px rgba(100, 120, 140, 0.3); 
}