/* ErrorPage.css */

/* Styling the container */
.error-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f8fafc;
  font-family: Arial, sans-serif;
}

/* Styling the content box */
.error-content {
  max-width: 400px;
  width: 100%;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* Heading for the error message */
.error-heading {
  font-size: 32px;
  font-weight: 700;
  color: #f44336; /* Red color */
  margin-bottom: 10px;
}

/* Error message styling */
.error-message {
  font-size: 18px;
  color: #333;
  margin-bottom: 20px;
}

/* Button container styling */
.button-container {
  margin-top: 20px;
}

/* Go back button styling */
.go-back-button {
  display: inline-block;
  padding: 12px 20px;
  background-color: #3b82f6; /* Blue color */
  color: white;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.go-back-button:hover {
  background-color: #2563eb; /* Darker blue on hover */
}

.go-back-button:focus {
  outline: none;
}

/* Support text styling */
.support-text {
  margin-top: 20px;
  font-size: 14px;
  color: #666;
}
